.page-body {
  font-family: "Open Sans Light", "Arial", sans-serif;
  font-size: 16px;
  line-height: 32px;
  letter-spacing: -0.01em;
  color: rgba(255, 255, 255, 0.3);
  height: 100%;
}

.center-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

.page-header {
  z-index: 1;
  margin: 0;
  padding: 21px 0 0 0;
}
@media (max-width: 1023px) {
  .page-header {
    min-width: 100%;
    padding: 0;
  }
}

@media (max-width: 1023px) {
  .page-header--background-color {
    background-color: #252525;
  }
}

.page-header__tablet {
  display: none;
  margin: 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .page-header__tablet {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (max-width: 1023px) {
  .page-header__tablet-inner {
    margin: 0;
    padding: 21px 0 0 21px;
  }
}
@media (max-width: 767px) {
  .page-header__tablet-inner {
    padding: 20px 0 0 17px;
  }
}

.page-header__navigation-item {
  background-color: #f6f6f6;
  margin: 0 0 30px 0;
  padding: 0;
  max-height: 60px;
}
.page-header__navigation-item:last-child {
  margin: 0;
}

.page-header__navigation-item--disappear {
  display: none;
}

.page-header__navigation-item p {
  margin: 0;
  padding: 15px;
  position: relative;
}

.page-header__navigation-item a {
  font-family: "Open Sans Semibold", "Arial", sans-serif;
  font-size: 17px;
  line-height: 28px;
  color: white;
  font-size: 20px;
  line-height: 28px;
  color: black;
}

.main-navigation {
  margin: 0;
  padding: 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
  min-width: 875px;
}
.main-navigation:hover {
  border-bottom: 1px solid #0ad9c6;
}
@media (max-width: 1023px) {
  .main-navigation {
    display: none;
  }
}

.main-navigation__list {
  font-family: "Open Sans Light", "Arial", sans-serif;
  font-size: 16px;
  line-height: 32px;
  letter-spacing: -0.01em;
  color: rgba(255, 255, 255, 0.3);
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  list-style: none;
  color: rgba(255, 255, 255, 0.3);
  letter-spacing: -0.01em;
  margin: 0 0 8px 0;
  padding: 0;
}

.main-navigation__list li {
  margin: 0 135px 0 0;
  padding: 0;
}
.main-navigation__list li:last-child {
  margin-right: 0;
}

.main-navigation__list-item:hover a {
  color: #0ad9c6;
}

.main-navigation__list-item:active a {
  opacity: 0.4;
}

.main-navigation__list-link {
  color: rgba(255, 255, 255, 0.3);
}

.burger-closed {
  display: none;
  cursor: pointer;
  transition: 1s all;
  z-index: 1;
}
@media (max-width: 1023px) {
  .burger-closed {
    display: none;
    justify-content: center;
    align-items: center;
    min-width: 50px;
    min-height: 50px;
    background-color: #0ad9c6;
    margin: 0;
    padding: 0;
  }
}
@media (max-width: 767px) {
  .burger-closed {
    min-width: 50px;
    min-height: 50px;
  }
}

.burger-closed--change-display {
  display: flex;
}

.burger-closed--background-color {
  background-color: white;
}

.burger-closed__inner {
  overflow: hidden;
  min-width: 22px;
  min-height: 22px;
}

.burger-closed__top-line {
  border: 1px solid black;
  width: 23px;
  padding: 0;
  margin: 0 0 5px 0;
  transition: 1s;
}

.burger-closed__middle-line {
  border: 1px solid black;
  width: 23px;
  padding: 0;
  margin: 0 0 5px 0;
  transition: 1s;
}

.burger-closed__bottom-line {
  transition: 1s;
}

.burger-closed__line {
  border: 1px solid black;
  width: 23px;
}

.burger-closed__line--active {
  transform: translate(200%);
  transition: 1s;
  margin: 0;
  padding: 0;
}

.burger-closed__top-line--transform {
  border: 1px solid rgba(20, 21, 23, 0.2);
  width: 23px;
  transform: rotate(135deg);
  position: relative;
  top: 9px;
  left: 0;
  transition: 1s;
}

.burger-closed__bottom-line--transform {
  border: 1px solid rgba(20, 21, 23, 0.2);
  width: 23px;
  transform: rotate(-135deg);
  position: relative;
  top: 2px;
  right: 0;
  transition: 1s;
}

.burger-open {
  display: none;
}
@media (max-width: 1023px) {
  .burger-open {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    min-height: 100%;
    transition: 1s;
    background-color: white;
  }
}

.burger-open--appear {
  display: block;
}

.burger-open__menu {
  width: 50px;
  height: 52px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  margin: 0 0 82px auto;
  padding: 0 20px 0 0;
}

.burger-open__list-item-wrapper {
  position: relative;
  background-color: #f6f6f6;
  margin: 0 0 30px 0;
  padding: 0;
  max-height: 60px;
}
.burger-open__list-item-wrapper:hover {
  background-color: #e2dede;
}
.burger-open__list-item-wrapper:active {
  background-color: #9e9898;
}
.burger-open__list-item-wrapper:last-child {
  margin: 0;
}
.burger-open__list-item-wrapper::after {
  content: "";
  position: absolute;
  top: 22px;
  right: 16px;
  background: url("../img/burger-arrow.svg") no-repeat;
  background-size: 25px 12px;
  width: 25px;
  height: 12px;
}

.burger-open__list-item {
  margin: 0;
  padding: 0;
  position: relative;
}

.burger-open__list-item a {
  font-family: "Open Sans Semibold", "Arial", sans-serif;
  font-size: 17px;
  line-height: 28px;
  color: white;
  display: block;
  font-size: 20px;
  line-height: 28px;
  color: black;
  padding: 15px;
}

.burger-open__action {
  display: none;
}

